@import url('https://rsms.me/inter/inter.css');

html {
  font-family: 'Inter', sans-serif;
}

@supports (font-variation-settings: normal) {
  html {
    font-family: 'Inter var', sans-serif;
  }
}

body {
  width: 100%;
}

.staking-stake-button {
  box-sizing: border-box;
  position: absolute;
  left: 40.96%;
  right: 40.96%;
  top: 84.65%;
  bottom: 7.75%;
  background: #D2D2F9;
  border: 1px solid #787878;
  border-radius: 3px;
}

.staking-stake-text {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 900;
  font-size: 14px;
  line-height: 17px;
  text-align: center;
  color: #FFFFFF;
}

